import React from 'react';
import PropTypes from 'prop-types';
import { Link, navigate } from 'gatsby';

const SiteLink = ({ children, to, ...other }) => {
  const navigateToSelf = (event, to) => {
    event.persist();
    // const { pathname } = event.target.parentNode;
    // console.log(event);
    // console.log(`To: ${pathname} | Current: ${event.view.location.pathname}`);

    if (to === document.location.pathname) {
      event.stopPropagation();
      navigate(to, { replace: true });
      return;
    }

    event.preventDefault();
    navigate(to);
  };

  const internal = /^\/(?!\/)/.test(to);

  if (internal) {
    return (
      <Link to={to} onClick={evt => navigateToSelf(evt, to)} {...other}>
        {children}
      </Link>
    );
  }

  return (
    <a href={to} rel="noopener noreferrer" target="_blank" {...other}>
      {children}
    </a>
  );
};

export default SiteLink;

SiteLink.propTypes = {
  to: PropTypes.string.isRequired,
};
